
    <template>
  <div id="formulario" class="colorblau">
    <h1>Ponte en contacto con nosotros</h1>
    <div class="colorText">
    <b-form @submit="onSubmit" @reset="onReset" v-if="show">
      <b-form-group
        id="input-group-1"
        label="Dirección email"
        label-for="input-1"
        description=""
      >
        <b-form-input
          id="input-1"
          v-model="form.email"
          type="email"
          placeholder="Introduce tu email"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-2" label="Tu nombre de usuario" label-for="input-2">
        <b-form-input
          id="input-2"
          v-model="form.name"
          placeholder="Introduce tu nombre"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-3" label="Razón de contacto" label-for="input-3">
        <b-form-select
          id="input-3"
          v-model="form.food"
          :options="foods"
          required
        ></b-form-select>
      </b-form-group>

        <div>
          <b-form-textarea
            id="textarea"
            v-model="text"
            placeholder="Explícanos aquí..."
            rows="3"
            max-rows="6"
          ></b-form-textarea>

          <pre class="mt-3 mb-0">{{  }}</pre>
        </div>

      <b-form-group id="input-group-4" v-slot="{ ariaDescribedby }">
        <b-form-checkbox-group
          v-model="form.checked"
          id="checkboxes-4"
          :aria-describedby="ariaDescribedby"
        >
          <b-form-checkbox value="me">Aceptar la
            <b-link href="#">Política de Privacidad</b-link> 
          </b-form-checkbox>
          <!--<b-form-checkbox value="that">Check that out</b-form-checkbox>-->
        </b-form-checkbox-group>
      </b-form-group>


      <b-button type="submit" variant="info">Enviar</b-button>
      <!--<b-button type="reset" variant="secondary">Limpiar</b-button>-->
    </b-form>
    <!--
    <b-card class="mt-3" header="Form Data Result">
      <pre class="m-0">{{ form }}</pre>
    </b-card>
    -->
  </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          //email: '',
          // name: '',
          //food: null,
          // checked: []
        },
        foods: [{text:'--Selecciona una de las siguientes opciones--', value: null }, 'Formar parte del Staff','Problemas con la cuenta', 'Reportar a un usuario', 'Colaborar en el noticiario','Otro'],
        show: true
      }
    },
    methods: {
      onSubmit(event) {
        event.preventDefault()
        alert(JSON.stringify(this.form))
      },
      onReset(event) {
        event.preventDefault()
        // Reset our form values
        this.form.email = ''
        this.form.name = ''
        this.form.food = null
        this.form.checked = []
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      }
    }
  }
</script>

<style scoped>
#formulario{
padding-left:40px;
padding-right:40px;
}
</style>
